import React from 'react';
import Burger from './Burger';
import Buttons from './Buttons';

function BurgerButtonComp(props) {
	return (
		<React.Fragment>
			{props.matches.small && <Burger />}

			{props.matches.large && (
				<React.Fragment>
					<Buttons />
				</React.Fragment>
			)}
		</React.Fragment>
	);
}

export default BurgerButtonComp;
