import React, { Suspense, lazy } from 'react';
import { Route, Switch, useHistory, withRouter } from 'react-router-dom';
import Media from 'react-media';
import { Spin } from 'antd';
import 'bootstrap/dist/css/bootstrap.min.css';

// Components
import HeaderMedia from './components/header/header-media/HeaderMedia';
import CreateAdd from './components/create-add/CreateAdd';
import Category from './components/category/Category';
import FilterComp from './components/filter/filter-comp/FilterComp';
import ButtonsComp from './components/buttons-component/ButtonsComp';
const Adds = lazy(() => import('./components/re-use/add/adds/Adds'));
const Add = lazy(() => import('./components/add/Add'));
const Login = lazy(() => import('./components/login/Login'));
const SignUp = lazy(() => import('./components/sign-up/SignUp'));
const AddPage = lazy(() => import('./components/add-page/AddPage'));
const RavidMan = lazy(() => import('./components/ravid-man/RavidMan'));

function App() {
	const [ showFilter, setShowFilter ] = React.useState(false);
	const location = useHistory().location;

	React.useEffect(
		() => {
			if (location.pathname.includes('/add')) {
				return setShowFilter(false);
			}
			switch (location.pathname) {
				case '/add':
					return setShowFilter(false);
				case '/signup':
					return setShowFilter(false);
				case '/login':
					return setShowFilter(false);
				case '/account':
					return setShowFilter(false);
				default:
					return setShowFilter(true);
			}
		},
		[ location ]
	);

	return (
		<React.Fragment>
			<HeaderMedia />
			<Category />
			<Media
				query="(max-width: 1024px)"
				render={() => {
					return showFilter ? (
						<div style={{ direction: 'rtl' }} className="d-flex overflow-auto">
							<FilterComp class="mr-2" />
							<ButtonsComp />
						</div>
					) : null;
				}}
			/>
			<Suspense
				fallback={
					<div className="d-flex justify-content-center align-items-center mt-5">
						<Spin />
					</div>
				}
			>
				<Switch>
					<Route exact path="/cars" component={Adds} />
					<Route exact path="/phones" component={Adds} />
					<Route exact path="/estate" component={Adds} />
					<Route exact path="/other" component={Adds} />
					<Route exact path="/cars/:location" component={Adds} />
					<Route exact path="/phones/:location" component={Adds} />
					<Route exact path="/estate/:location" component={Adds} />
					<Route exact path="/other/:location" component={Adds} />
					<Route exact path="/category" component={Category} />
					{localStorage.getItem('token') !== null ? <Route exact path="/add" component={Add} /> : null}
					{localStorage.getItem('token') !== null ? (
						<Route exact path="/account" component={RavidMan} />
					) : null}
					{localStorage.getItem('token') === null ? <Route exact path="/login" component={Login} /> : null}
					{localStorage.getItem('token') === null ? <Route exact path="/signup" component={SignUp} /> : null}
					<Route exact path="/add/:id" component={AddPage} />
					<Route exact path="/:location" component={Adds} />
					<Route exact path="/category/:subcategory" component={Adds} />
					<Route exact path="/category/:location/:subcategory" component={Adds} />
					<Route exact path="/search/:search" component={Adds} />
					<Route exact path="/search/:location/:search" component={Adds} />
					<Route exact path="/search/category/:subcategory/:search" component={Adds} />
					<Route exact path="/search/category/:subcategory/:location/:search" component={Adds} />
					<Route exact path="/" component={Adds} />
				</Switch>
			</Suspense>
			{localStorage.getItem('token') !== null ? (
				<Media query="(max-width: 1024px)" render={() => <CreateAdd />} />
			) : null}
		</React.Fragment>
	);
}

export default withRouter(App);
