import React from 'react';

function P(props) {
	return (
		<p
			className={props.class}
			style={{
				width: '100%',
				textAlign: props.textAlign || 'right',
				fontSize: props.fontSize || '1.3em',
				margin: props.margin,
				padding: props.padding
			}}
		>
			{props.children}
		</p>
	);
}

export default P;
