import {
	ACTION,
	DRAWER_OPEN,
	FETCH_DATA,
	DRAWER_CLOSE,
	ADD_MAX_PRICE,
	ADD_MIN_PRICE,
	ADD_LOCATION,
	ADD_SUBCATEGORY,
	SET_URL,
	SET_START,
	SET_END,
	SET_LEN,
	SET_SPIN,
	SET_START_END,
	SET_AXIOS_OBJ,
	SET_AXIOS_URL
} from './type';

export const action = () => {
	return {
		type: ACTION
	};
};

export const drawerOpen = () => {
	return {
		type: DRAWER_OPEN
	};
};
export const drawerClose = () => {
	return {
		type: DRAWER_CLOSE
	};
};
export const addMaxPrice = (price) => {
	return {
		type: ADD_MAX_PRICE,
		payload: price
	};
};
export const addMinPrice = (price) => {
	return {
		type: ADD_MIN_PRICE,
		payload: price
	};
};
export const addLocation = (location) => {
	return {
		type: ADD_LOCATION,
		payload: location
	};
};
export const addSubcategory = (subcategory) => {
	return {
		type: ADD_SUBCATEGORY,
		payload: subcategory
	};
};
export const fetchData = (data) => {
	return {
		type: FETCH_DATA,
		payload: data
	};
};
export const setUrl = (url) => {
	return {
		type: SET_URL,
		payload: url
	};
};
export const setStart = () => {
	return {
		type: SET_START
	};
};
export const setEnd = () => {
	return {
		type: SET_END
	};
};
export const setSpin = (spin) => {
	return {
		type: SET_SPIN,
		payload: spin
	};
};
export const setLen = (len) => {
	return {
		type: SET_LEN,
		payload: len
	};
};
export const setStartEnd = () => {
	return {
		type: SET_START_END
	};
};
export const setAxiosObj = (obj) => {
	return {
		type: SET_AXIOS_OBJ,
		payload: obj
	};
};
export const setAxiosUrl = (url) => {
	return {
		type: SET_AXIOS_URL,
		payload: url
	};
};
