import { subCats } from '../../app-data/subcats';

export const handlePlaceholder = (appLocation, subcategory, url) => {
	const jost = 'جستجو';
	const dar = 'در';
	const daste = 'دسته';

	switch (url) {
		case '/cars':
			const mashin = 'ماشین';
			return `${jost} ${dar} ${daste} ${mashin}`;
		case '/phones':
			const mobile = 'گوشی';
			return `${jost} ${dar} ${daste} ${mobile}`;
		case '/estate':
			const estate = 'املاک';
			return `${jost} ${dar} ${daste} ${estate}`;
		case '/other':
			const other = 'متفرقه';
			return `${jost} ${dar} ${daste} ${other}`;
	}

	if (appLocation && subcategory) {
		const item = subCats.find((any) => any.url === subcategory);
		return `${jost} ${dar} ${daste} ${item.name} ${dar} ${appLocation}`;
	}
	if (subcategory) {
		const item = subCats.find((any) => any.url === subcategory);
		return `${jost} ${dar} ${item.name}`;
	}
	if (appLocation) {
		return `${jost} ${dar} ${appLocation}`;
	}
	return 'جستجو در همه ی آگهی ها';
};
