import React from 'react';
import classes from './CreateAdd.module.scss';
import { PlusOutlined } from '@ant-design/icons';
import { withRouter } from 'react-router-dom';
import { useHistory } from 'react-router-dom';

function CreateAdd(props) {
	const url = useHistory().location;

	const func = () => {
		if (url.pathname !== '/add') {
			return classes.add;
		}
		return classes.hide;
	};
	const clas = func();

	return (
		<div onClick={() => props.history.push('/add')} className={`${clas}`}>
			<PlusOutlined style={{ fontSize: '30px', color: 'white' }} />
		</div>
	);
}

export default withRouter(CreateAdd);
