import React from 'react';
import { Drawer } from 'antd';

// Component
import CatMenu from './CatMenu';
import classes from './Category.module.scss';

// Redux
import { connect } from 'react-redux';
import { drawerOpen, drawerClose } from '../../redux/action';

function Category(props) {
	const onClose = () => {
		props.drawerClose();
	};

	return (
		<React.Fragment>
			<Drawer
				className={classes.drawer}
				title="دسته بندی ها"
				placement={'right'}
				closable={false}
				onClose={onClose}
				visible={props.drawer}
			>
				<CatMenu close={onClose} />
			</Drawer>
		</React.Fragment>
	);
}

const mapState = (state) => {
	return {
		drawer: state.drawer
	};
};
const mapDis = (dispatch) => {
	return {
		drawerClose: () => dispatch(drawerClose()),
		drawerOpen: () => dispatch(drawerOpen())
	};
};

export default connect(mapState, mapDis)(Category);
