import queryString from 'query-string';

const minPriceHandler = (minPrice) => {
	const sad = `100`;
	const divist = '200';
	const punsad = '500';
	const hezar = 'هزار تومان';
	if (minPrice === '100000') {
		return `${sad} ${hezar}`;
	} else if (minPrice === '200000') {
		return `${divist} ${hezar}`;
	} else {
		return `${punsad} ${hezar}`;
	}
};

const maxPriceHandler = (maxPrice) => {
	const dah = `10`;
	const si = '30';
	const haftad = '70';
	const million = 'میلیون تومان';
	if (maxPrice === '10000000') {
		return `${dah} ${million}`;
	} else if (maxPrice === '30000000') {
		return `${si} ${million}`;
	} else {
		return `${haftad} ${million}`;
	}
};

export const textHandler = (location) => {
	const query = queryString.parse(location.search);
	let minPrice = query.minPrice;
	let maxPrice = query.maxPrice;
	if (minPrice && maxPrice) {
		minPrice = minPriceHandler(minPrice);
		maxPrice = maxPriceHandler(maxPrice);
		const start = `قیمت از`;
		const ta = `تا`;
		return `${start} ${minPrice} ${ta} ${maxPrice}`;
	}
	if (minPrice && !maxPrice) {
		minPrice = minPriceHandler(minPrice);
		const start = `قیمت از`;
		const ta = `تا`;
		const harchi = `هرچقدر`;
		return `${start} ${minPrice} ${ta} ${harchi}`;
	}
	if (maxPrice && !minPrice) {
		maxPrice = maxPriceHandler(maxPrice);
		const start = `قیمت`;
		const ta = `تا`;
		return `${start} ${ta} ${maxPrice}`;
	}
	return '';
};
