import React from 'react';
import { CloseCircleOutlined } from '@ant-design/icons';
import { Button } from 'antd';
import { withRouter, useHistory } from 'react-router-dom';
import { subCats } from '../../app-data/subcats';
import options from '../../app-data/options';
import { textHandler } from './price-logic';
import classes from './ButtonsComp.module.scss';

// Redux
import { connect } from 'react-redux';
import { addLocation, addSubcategory } from '../../redux/action';

function ButtonsComp(props) {
	const location = useHistory().location;
	const text = textHandler(location);

	const handler = () => {
		props.history.push(props.location.pathname);
	};
	const handleSubcatBtn = () => {
		if (props.subcategory) {
			const sub = subCats.find((any) => any.url === props.subcategory);
			return `${sub.name}`;
		}
		return '';
	};
	const subCatBtn = handleSubcatBtn();

	const handleLocationClick = () => {
		props.addLocation('');
		props.history.push(`/${location.search}`);
	};

	const handleSubcategoryClick = () => {
		props.addSubcategory('');
		if (props.appLocation) {
			const loc = options.find((any) => any.value === props.appLocation);
			props.history.push(`/${loc.url}${location.search}`);
		} else {
			props.history.push(`/${location.search}`);
		}
	};
	return (
		<React.Fragment>
			{props.appLocation ? (
				<Button
					type="primary"
					className="mr-3 mt-2 mb-2"
					size="large"
					shape="round"
					icon={<CloseCircleOutlined className={classes.icon} />}
					onClick={handleLocationClick}
				>
					{props.appLocation}
				</Button>
			) : null}
			{props.subcategory ? (
				<Button
					type="primary"
					className="mr-3 mt-2 mb-2"
					size="large"
					shape="round"
					icon={<CloseCircleOutlined className={classes.icon} />}
					onClick={handleSubcategoryClick}
				>
					{subCatBtn}
				</Button>
			) : null}
			{props.location.search ? (
				<Button
					type="primary"
					className="mr-3 mt-2 mb-2"
					onClick={handler}
					size="large"
					shape="round"
					icon={<CloseCircleOutlined className={classes.icon} />}
				>
					{text}
				</Button>
			) : null}
		</React.Fragment>
	);
}

const mapState = (state) => {
	return {
		appLocation: state.appLocation,
		subcategory: state.subcategory
	};
};

const mapDis = (dispatch) => {
	return {
		addLocation: (location) => dispatch(addLocation(location)),
		addSubcategory: (subcategory) => dispatch(addSubcategory(subcategory))
	};
};

export default connect(mapState, mapDis)(withRouter(ButtonsComp));
