import React from 'react';
import Media from 'react-media';
import { Button } from 'antd';
import classes from './Header.module.scss';
import City from './city/City';
import BurgerButtonComp from './burger-button-comp/BurgerButtonComp';
import { withRouter } from 'react-router-dom';

function Header(props) {
	const handleClick = () => {
		props.history.push(`/`);
	};
	return (
		<div className={classes.header}>
			<div className={classes.header__buttons}>
				<Button onClick={handleClick} className="text-light p-0 m-0" type="link">
					<p className={classes.logo}>{props.logoText}</p>
				</Button>
				<City placeholder={`شهر`} textAlign={`center`} width={120} class="mr-3" />
			</div>

			<div className={classes.header__buttons}>
				<Media
					queries={{
						small: '(max-width: 1023px)',
						large: '(min-width: 1023px)'
					}}
				>
					{(matches) => <BurgerButtonComp matches={matches} />}
				</Media>
			</div>
		</div>
	);
}

export default withRouter(Header);
