import React from 'react';
import { ConfigProvider } from 'antd';
import Empty from '../../re-use/ant-empty/Empty';
import SelectComp from './SelectComp';

function City(props) {
	const classProp = props.class;
	const formProps = props.props;
	const formProp = formProps ? formProps : null;

	return (
		<ConfigProvider renderEmpty={() => <Empty text={`یافت نشد`} textAlign={`center`} iconSize={25} />}>
			<div className="config-provider">
				<SelectComp
					textAlign={props.textAlign}
					placeholder={props.placeholder}
					width={props.width}
					class={classProp}
					props={formProp}
				/>
			</div>
		</ConfigProvider>
	);
}

export default City;
