import React, { useState, useEffect } from 'react';
import { Input } from 'antd';
import { SearchOutlined } from '@ant-design/icons';
import { useHistory, withRouter } from 'react-router-dom';
import Media from 'react-media';
import classes from './SearchComp.module.scss';
import options from '../../app-data/options';
import { handlePlaceholder } from './placeholder-logic';

// Redux
import { connect } from 'react-redux';

// Components
import FilterComp from '../filter/filter-comp/FilterComp';
import ButtonsComp from '../buttons-component/ButtonsComp';

function SearchComp(props) {
	const [ searchVal, setSearchVal ] = useState('');
	const [ showFilter, setShowFilter ] = useState(false);
	const location = useHistory().location;

	useEffect(
		() => {
			if (location.pathname.includes('/add')) {
				return setShowFilter(false);
			}
			switch (location.pathname) {
				case '/add':
					return setShowFilter(false);
				case '/signup':
					return setShowFilter(false);
				case '/login':
					return setShowFilter(false);
				case '/account':
					return setShowFilter(false);
				default:
					return setShowFilter(true);
			}
		},
		[ location ]
	);

	const handleSearch = () => {
		const { appLocation, subcategory, history } = props;
		if (appLocation && subcategory) {
			const item = options.find((any) => any.value === appLocation);
			return history.push(`/search/category/${subcategory}/${item.url}/${searchVal}`);
		}
		if (appLocation) {
			const item = options.find((any) => any.value === appLocation);
			return history.push(`/search/${item.url}/${searchVal}`);
		}
		if (subcategory) {
			return history.push(`/search/category/${subcategory}/${searchVal}`);
		}
		history.push(`/search/${searchVal}`);
	};

	const placeholder = handlePlaceholder(props.appLocation, props.subcategory, location.pathname);

	return (
		<div className={classes.div}>
			<Input
				onChange={(e) => {
					setSearchVal(e.target.value);
				}}
				onPressEnter={handleSearch}
				value={searchVal}
				className={classes.input}
				placeholder={placeholder}
				allowClear
				enterButton={true}
				suffix={
					<div onClick={handleSearch} style={{ cursor: 'pointer' }}>
						<SearchOutlined style={{ fontSize: 20 }} />
					</div>
				}
			/>
			<Media
				query="(min-width: 1024px)"
				render={() => {
					return showFilter ? (
						<div style={{ direction: 'rtl', width: '600px' }} className="d-flex overflow-auto">
							<FilterComp />
							<ButtonsComp />
						</div>
					) : null;
				}}
			/>
		</div>
	);
}

const mapState = (state) => {
	return {
		appLocation: state.appLocation,
		subcategory: state.subcategory
	};
};
const mapDis = (dispatch) => {
	return {};
};

export default connect(mapState, mapDis)(withRouter(SearchComp));
