import {
	ACTION,
	DRAWER_CLOSE,
	DRAWER_OPEN,
	ADD_MIN_PRICE,
	ADD_MAX_PRICE,
	ADD_LOCATION,
	ADD_SUBCATEGORY,
	FETCH_DATA,
	SET_URL,
	SET_START,
	SET_END,
	SET_LEN,
	SET_SPIN,
	SET_START_END,
	SET_AXIOS_OBJ,
	SET_AXIOS_URL
} from './type';

const initialState = {
	drawer: false,
	minPrice: null,
	maxPrice: null,
	appLocation: '',
	subcategory: '',
	number: 1,
	token: 'token',
	id: 'id',
	fetchedData: [],
	url: '',
	start: 0,
	end: 10,
	len: null,
	spin: false,
	axiosUrl: '',
	axiosObj: ''
};

export const reducer = (state = initialState, action) => {
	switch (action.type) {
		case ACTION:
			console.log('Action called');
			return state;
		case DRAWER_CLOSE:
			return {
				...state,
				drawer: false
			};
		case DRAWER_OPEN:
			return {
				...state,
				drawer: true
			};
		case ADD_MAX_PRICE:
			return {
				...state,
				maxPrice: action.payload
			};
		case ADD_MIN_PRICE:
			return {
				...state,
				minPrice: action.payload
			};
		case ADD_LOCATION:
			return {
				...state,
				appLocation: action.payload
			};
		case ADD_SUBCATEGORY:
			return {
				...state,
				subcategory: action.payload
			};
		case FETCH_DATA:
			return {
				...state,
				fetchedData: action.payload
			};
		case SET_URL:
			return {
				...state,
				url: action.payload
			};
		case SET_START:
			return {
				...state,
				start: state.end
			};
		case SET_END:
			return {
				...state,
				end: state.end + 10
			};
		case SET_LEN:
			return {
				...state,
				len: action.payload
			};
		case SET_SPIN:
			return {
				...state,
				spin: action.payload
			};
		case SET_START_END:
			return {
				...state,
				start: 0,
				end: 10
			};
		case SET_AXIOS_OBJ:
			return {
				...state,
				axiosObj: action.payload
			};
		case SET_AXIOS_URL:
			return {
				...state,
				axiosUrl: action.payload
			};
		default:
			return state;
	}
};

export default reducer;
