import React from 'react';
import { Modal, Button } from 'antd';

function ModalComp(props) {
	return (
		<Modal
			title="فیلتر"
			visible={props.visible}
			onOk={props.handleOk}
			onCancel={props.handleCancel}
			footer={[
				<Button size="large" key="back" onClick={props.handleCancel}>
					بازگشت
				</Button>,
				<Button type="danger" size="large" key="delete" onClick={props.handleDeleteFilter}>
					حذف فیلتر
				</Button>,
				<Button size="large" key="submit" type="primary" onClick={props.handleOk}>
					اعمال فیلتر
				</Button>
			]}
		>
			{props.children}
		</Modal>
	);
}

export default ModalComp;
