import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Menu } from 'antd';
import { MobileOutlined, CarOutlined, HomeOutlined, EllipsisOutlined } from '@ant-design/icons';
import classes from './CatMenu.module.scss';
import options from '../../app-data/options';

// Redux
import { connect } from 'react-redux';
import { addSubcategory } from '../../redux/action';

const { SubMenu } = Menu;

export class CatMenu extends Component {
	constructor(props) {
		super(props);

		this.state = {
			rootSubmenuKeys: [ 'sub1', 'sub2', 'sub3', 'sub4' ],
			openKeys: []
		};
	}

	onOpenChange = (openKeys) => {
		const latestOpenKey = openKeys.find((key) => this.state.openKeys.indexOf(key) === -1);
		if (this.state.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
			this.setState({ openKeys });
		} else {
			this.setState({
				openKeys: latestOpenKey ? [ latestOpenKey ] : []
			});
		}
	};

	onClick = (e) => {
		this.props.close();
		let url = `/category`;
		if (this.props.appLocation) {
			const item = options.find((any) => any.value === this.props.appLocation);
			url = `/category/${item.url}`;
		}
		switch (e.key) {
			case '1':
				this.props.addSubcategory('domestic');
				return this.props.history.push(`${url}/domestic`);
			case '2':
				this.props.addSubcategory('foreign');
				return this.props.history.push(`${url}/foreign`);
			case '3':
				this.props.addSubcategory('collection');
				return this.props.history.push(`${url}/collection`);
			case '4':
				this.props.addSubcategory('benz');
				return this.props.history.push(`${url}/benz`);
			case '5':
				this.props.addSubcategory('bmw');
				return this.props.history.push(`${url}/bmw`);
			case '6':
				this.props.addSubcategory('');
				if (this.props.appLocation) {
					const item = options.find((any) => any.value === this.props.appLocation);
					return this.props.history.push(`/cars/${item.url}`);
				}
				return this.props.history.push(`/cars`);

			case '7':
				this.props.addSubcategory('iphone');
				return this.props.history.push(`${url}/iphone`);
			case '8':
				this.props.addSubcategory('android');
				return this.props.history.push(`${url}/android`);
			case '9':
				this.props.addSubcategory('');
				if (this.props.appLocation) {
					const item = options.find((any) => any.value === this.props.appLocation);
					return this.props.history.push(`/phones/${item.url}`);
				}
				return this.props.history.push(`/phones`);
			case '10':
				this.props.addSubcategory('rent');
				return this.props.history.push(`${url}/rent`);
			case '11':
				this.props.addSubcategory('buy');
				return this.props.history.push(`${url}/buy`);
			case '12':
				this.props.addSubcategory('');
				if (this.props.appLocation) {
					const item = options.find((any) => any.value === this.props.appLocation);
					return this.props.history.push(`/estate/${item.url}`);
				}
				return this.props.history.push(`/estate`);
			case '13':
				this.props.addSubcategory('');
				if (this.props.appLocation) {
					const item = options.find((any) => any.value === this.props.appLocation);
					return this.props.history.push(`/other/${item.url}`);
				}
				return this.props.history.push(`/other`);
			default:
				return undefined;
		}
	};

	render() {
		return (
			<Menu
				className={classes.items}
				mode="inline"
				openKeys={this.state.openKeys}
				onOpenChange={this.onOpenChange}
				style={{ width: 256 }}
			>
				<SubMenu key="sub1" icon={<CarOutlined style={{ fontSize: 18 }} />} title="ماشین">
					<Menu.Item onClick={this.onClick} key="1">
						ایرانی
					</Menu.Item>
					<Menu.Item onClick={this.onClick} key="2">
						خارجی
					</Menu.Item>
					<Menu.Item onClick={this.onClick} key="3">
						کلکسیونی
					</Menu.Item>
					<Menu.Item onClick={this.onClick} key="4">
						بنز
					</Menu.Item>
					<Menu.Item onClick={this.onClick} key="5">
						بی ام و
					</Menu.Item>
					<Menu.Item onClick={this.onClick} key="6">
						همه ی آگهی ها
					</Menu.Item>
				</SubMenu>
				<SubMenu key="sub2" icon={<MobileOutlined style={{ fontSize: 18 }} />} title="موبایل">
					<Menu.Item onClick={this.onClick} key="7">
						ایفون
					</Menu.Item>
					<Menu.Item onClick={this.onClick} key="8">
						اندروید
					</Menu.Item>
					<Menu.Item onClick={this.onClick} key="9">
						همه ی آگهی ها
					</Menu.Item>
				</SubMenu>
				<SubMenu key="sub3" icon={<HomeOutlined style={{ fontSize: 18 }} />} title="مسکن">
					<Menu.Item onClick={this.onClick} key="10">
						اجاره
					</Menu.Item>
					<Menu.Item onClick={this.onClick} key="11">
						خرید و فروش
					</Menu.Item>
					<Menu.Item onClick={this.onClick} key="12">
						همه ی آگهی ها
					</Menu.Item>
				</SubMenu>
				<SubMenu key="sub4" icon={<EllipsisOutlined style={{ fontSize: 18 }} />} title="دیگر">
					<Menu.Item onClick={this.onClick} key="13">
						متفرقه
					</Menu.Item>
				</SubMenu>
			</Menu>
		);
	}
}

const mapState = (state) => {
	return {
		appLocation: state.appLocation
	};
};

const mapDis = (dispatch) => {
	return {
		addSubcategory: (subcategory) => dispatch(addSubcategory(subcategory))
	};
};
export default connect(mapState, mapDis)(withRouter(CatMenu));
