import React from 'react';
import { Dropdown, Menu } from 'antd';
import { AlignLeftOutlined } from '@ant-design/icons';
import { withRouter } from 'react-router-dom';

// Redux
import { connect } from 'react-redux';
import { drawerOpen, drawerClose } from '../../../redux/action';

const dropdown = (props) => {
	const goToContactPage = () => {
		console.log('go to contact page');
	};
	const signOut = () => {
		localStorage.clear();
		props.history.push('/');
	};
	const showDrawer = () => {
		props.drawerOpen();
	};
	const goToProfilePage = () => {
		props.history.push('/account');
	};
	const goToAccountPage = () => {
		props.history.push('/login');
	};
	return (
		<Menu>
			{localStorage.getItem('token') === null ? (
				<Menu.Item onClick={goToAccountPage} style={{ textAlign: 'right' }}>
					<h5 style={{ fontSize: '1.35em' }} className="p-0 m-1">
						ورود به حساب
					</h5>
				</Menu.Item>
			) : null}
			<Menu.Item onClick={showDrawer} style={{ textAlign: 'right' }}>
				<h5 style={{ fontSize: '1.35em' }} className="p-0 m-1">
					دسته بندی ها
				</h5>
			</Menu.Item>
			{localStorage.getItem('token') !== null ? (
				<Menu.Item onClick={goToProfilePage} style={{ textAlign: 'right' }}>
					<h5 style={{ fontSize: '1.35em' }} className="p-0 m-1">
						راوید من
					</h5>
				</Menu.Item>
			) : null}
			<Menu.Item onClick={goToContactPage} style={{ textAlign: 'right' }}>
				<h5 style={{ fontSize: '1.35em' }} className="p-0 m-1">
					تماس با ما
				</h5>
			</Menu.Item>
			{localStorage.getItem('token') !== null ? (
				<Menu.Item onClick={signOut} style={{ textAlign: 'right' }}>
					<h5 style={{ fontSize: '1.35em' }} className="p-0 m-1">
						خروج
					</h5>
				</Menu.Item>
			) : null}
		</Menu>
	);
};

function Burger(props) {
	return (
		<Dropdown trigger={[ 'click' ]} overlay={dropdown(props)}>
			<AlignLeftOutlined style={{ fontSize: 20, color: 'white', cursor: 'pointer' }} />
		</Dropdown>
	);
}

const mapState = (state) => {
	return {
		drawer: state.drawer
	};
};
const mapDis = (dispatch) => {
	return {
		drawerClose: () => dispatch(drawerClose()),
		drawerOpen: () => dispatch(drawerOpen())
	};
};

export default connect(mapState, mapDis)(withRouter(Burger));
