import React from 'react';
import { withRouter, useHistory } from 'react-router-dom';

// Redux
import { connect } from 'react-redux';
import { addMaxPrice, addMinPrice } from '../../redux/action';

// Components
import BootstrapContainer from '../re-use/bootstrap-container/BootstrapContainer';
import AddContainer from '../re-use/add/AddContainer';
import AddItemContainer from '../re-use/add/AddItemContainer';
import ModalComp from './modal/ModalComp';
import MaxPrice from './max-price/MaxPrice';
import MinPrice from './min-price/MinPrice';
import FilterButton from './filter-btn/FilterButton';

function Filter(props) {
	const location = useHistory().location.pathname;

	const [ visible, setVisible ] = React.useState(false);

	const priceHandler = () => {
		if (props.minPrice && props.maxPrice) {
			return `?minPrice=${props.minPrice}&maxPrice=${props.maxPrice}`;
		}
		if (props.maxPrice) {
			return `?maxPrice=${props.maxPrice}`;
		}
		if (props.minPrice) {
			return `?minPrice=${props.minPrice}`;
		}
		return '';
	};
	const price = priceHandler();

	const showModal = () => {
		setVisible(true);
	};

	const handleOk = () => {
		setVisible(false);
		props.history.push(`${location}${price}`);
	};

	const handleDeleteFilter = () => {
		props.addMinPrice(null);
		props.addMaxPrice(null);
		props.history.push(`${location}`);
		setVisible(false);
	};

	const handleCancel = () => {
		setVisible(false);
	};

	const handleChangeMinPrice = (price) => {
		props.addMinPrice(price);
	};
	const handleChangeMaxPrice = (price) => {
		props.addMaxPrice(price);
	};
	return (
		<div className={`d-flex justify-content-end mt-2 mb-2 ${props.class}`}>
			<FilterButton text={`فیلتر`} showModal={showModal} />
			<ModalComp
				visible={visible}
				handleCancel={handleCancel}
				handleDeleteFilter={handleDeleteFilter}
				handleOk={handleOk}
			>
				<BootstrapContainer>
					<AddContainer>
						<AddItemContainer class="align-items-center">
							<MinPrice handleChangeMinPrice={handleChangeMinPrice} minPrice={props.minPrice} />
						</AddItemContainer>
					</AddContainer>
					<AddContainer>
						<AddItemContainer class="align-items-center">
							<MaxPrice handleChangeMaxPrice={handleChangeMaxPrice} maxPrice={props.maxPrice} />
						</AddItemContainer>
					</AddContainer>
				</BootstrapContainer>
			</ModalComp>
		</div>
	);
}

const mapState = (state) => {
	return {
		maxPrice: state.maxPrice,
		minPrice: state.minPrice
	};
};

const mapDis = (dispatch) => {
	return {
		addMaxPrice: (price) => dispatch(addMaxPrice(price)),
		addMinPrice: (price) => dispatch(addMinPrice(price))
	};
};

export default connect(mapState, mapDis)(withRouter(Filter));
