import React from 'react';
import { Button } from 'antd';
import { withRouter } from 'react-router-dom';

// Redux
import { connect } from 'react-redux';
import { drawerOpen } from '../../../redux/action';

function Buttons(props) {
	const [ btn1, setBtn1 ] = React.useState(false);
	const [ btn2, setBtn2 ] = React.useState(false);
	const [ btn3, setBtn3 ] = React.useState(false);
	const [ btn4, setBtn4 ] = React.useState(false);
	const [ btn5, setBtn5 ] = React.useState(false);
	const [ btn6, setBtn6 ] = React.useState(false);
	return (
		<div>
			<Button
				className={btn3 ? `border-bottom text-light mr-1 ml-1` : `mr-1 ml-1`}
				onMouseEnter={() => setBtn3(true)}
				onMouseLeave={() => setBtn3(false)}
				type="link"
				style={{ height: '40px ', fontSize: '1.4em' }}
			>
				تماس با ما
			</Button>
			{localStorage.getItem('token') === null ? (
				<Button
					className={btn6 ? `border-bottom text-light mr-1 ml-1` : `mr-1 ml-1`}
					onClick={() => props.history.push('/login')}
					onMouseEnter={() => setBtn6(true)}
					onMouseLeave={() => setBtn6(false)}
					type="link"
					style={{ height: '40px ', fontSize: '1.4em' }}
				>
					ورود به حساب
				</Button>
			) : null}
			<Button
				className={btn5 ? `border-bottom text-light mr-1 ml-1` : `mr-1 ml-1`}
				onMouseEnter={() => setBtn5(true)}
				onMouseLeave={() => setBtn5(false)}
				type="link"
				style={{ height: '40px ', fontSize: '1.4em' }}
				onClick={() => props.drawerOpen()}
			>
				دسته بندی ها
			</Button>
			{localStorage.getItem('token') !== null ? (
				<React.Fragment>
					<Button
						className={btn2 ? `border-bottom text-light mr-1 ml-1` : `mr-1 ml-1`}
						onMouseEnter={() => setBtn2(true)}
						onMouseLeave={() => setBtn2(false)}
						onClick={() => props.history.push('/account')}
						type="link"
						style={{ height: '40px ', fontSize: '1.4em' }}
					>
						راوید من
					</Button>
					<Button
						className={btn1 ? `border-bottom text-light mr-1 ml-1` : `mr-1 ml-1 `}
						onClick={() => {
							localStorage.clear();
							props.history.push('/');
						}}
						onMouseEnter={() => setBtn1(true)}
						onMouseLeave={() => setBtn1(false)}
						type="link"
						style={{ height: '40px ', fontSize: '1.4em' }}
					>
						خروج
					</Button>
					<Button
						className={btn4 ? `border border-danger mr-1 ml-1 pb-3` : `mr-1 ml-1 pb-3 `}
						onMouseEnter={() => setBtn4(true)}
						onMouseLeave={() => setBtn4(false)}
						onClick={() => props.history.push('/add')}
						type="primary"
						style={{ height: '40px ', fontSize: '1.4em' }}
						danger
					>
						<p className="m-0 p-0">ثبت آگهی</p>
					</Button>
				</React.Fragment>
			) : null}
		</div>
	);
}

const mapState = (state) => {
	return {
		drawer: state.drawer
	};
};
const mapDis = (dispatch) => {
	return {
		drawerOpen: () => dispatch(drawerOpen())
	};
};

export default connect(mapState, mapDis)(withRouter(Buttons));
