import React from 'react';
import { withRouter } from 'react-router-dom';
import { Select } from 'antd';
import cities from '../../../app-data/options';
import { ErrorMessage } from 'formik';
import P from '../../re-use/p/P';

// Redux
import { connect } from 'react-redux';
import { addLocation } from '../../../redux/action';

const { Option } = Select;

function SelectComp(props) {
	const handleCity = (e) => {
		const { history, subcategory, addLocation } = props;
		const item = cities.find((any) => any.url === e);
		addLocation(item.value);
		if (subcategory) {
			return history.push(`/category/${e}/${subcategory}`);
		}
		history.push(`/${e}`);
	};

	const handleChange = (e) => {
		props.props.setFieldValue('location', e);
	};

	const handleErr = () => {
		if (props.props && props.props.touched.location && props.props && props.props.errors.location) {
			return 'border border-danger ml-5';
		}
		return '';
	};
	const cls = handleErr();

	const onchange = (e) => {
		const item = cities.find((opt) => opt.value === e);
		if (props.props) {
			handleChange(item.url);
		} else {
			handleCity(item.url);
		}
	};

	return (
		<React.Fragment>
			<Select
				showSearch
				className={`${props.class} ${cls}`}
				style={{ width: props.width, textAlign: props.textAlign }}
				placeholder={props.placeholder}
				onChange={(e) => onchange(e)}
				onBlur={() => {
					if (props.props) {
						props.props && props.props.setFieldTouched('location');
					}
				}}
				value={props.appLocation || `شهر`}
			>
				{cities.map((each, index) => {
					return (
						<Option key={index} style={{ textAlign: 'right' }} value={each.value}>
							<h5 style={{ fontWeight: '300' }} className="  p-0 m-1">
								{each.value}
							</h5>
						</Option>
					);
				})}
			</Select>
			{props.props ? (
				<P class="mt-2 text-danger">
					<ErrorMessage name="location" />
				</P>
			) : null}
		</React.Fragment>
	);
}

const mapState = (state) => {
	return {
		subcategory: state.subcategory,
		appLocation: state.appLocation
	};
};
const mapDis = (dispatch) => {
	return {
		addLocation: (location) => dispatch(addLocation(location))
	};
};

export default connect(mapState, mapDis)(withRouter(SelectComp));
